@keyframes fade-to-right {
   from {
      transform: translateX(0%);
      visibility: visible;
      // opacity: 0.6;
   }
   to {
      // opacity: 1;
      visibility: hidden;
      transform: translateX(-100%);
   }
}

@keyframes fade-to-left {
   from {
      // opacity: 1;
      transform: translateX(0%);
   }
   to {
      // opacity: 0;
      transform: translateX(-100%);
   }
}

.carousel {
   height: 30rem;
   width: 100%;
   margin-top: 1rem;

   &-items {
      display: flex;
      overflow-x: scroll;
      position: relative;
      height: 100%;

      &::-webkit-scrollbar {
         display: none;
      }

      &-item {
         position: relative;
         padding: 2rem;
         min-width: 100%;
         min-height: 100%;
         height: 100%;
         border-radius: $border-radius;
         z-index: 1;
         background-color: transparent;
         margin-right: 1rem;
         transition: all 300ms ease-in-out;
         scroll-behavior: smooth;
         transition: 400ms;
         //
         border-radius: $border-radius;
         padding: 2rem;
         line-height: 2.5rem;

         &.right {
            animation-name: fade-to-right;
            animation-duration: 800ms;
            animation-delay: 100ms;
            animation-timing-function: ease-in-out;
            animation-fill-mode: forwards;
            animation-iteration-count: infinite;
            // left: -100%;
            // animation-name: fade-to-left;
            // animation-duration: 1s;
            // animation-delay: 200ms;
            // animation-timing-function: ease-in-out;
         }

         img {
            border-radius: $border-radius;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: -1;
         }

         &_head {
            text-transform: capitalize;
            font-weight: 700;
            font-size: 1.5rem;
         }

         &_body {
            font-weight: lighter;
            text-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow;
         }

         &_shop-now {
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: capitalize;
            background-color: $main-color;
            border-radius: $border-radius * 0.75;
            margin-top: 0.5rem;
            width: 7rem;
            height: 2.5rem;
            text-align: center;
            color: white;
            transition: 300ms;
            font-size: 0.85rem;
            cursor: pointer;
            transition: all 300ms ease-in-out;

            &:hover {
               color: white;
               box-shadow: 1px grey;
               background-color: $dark-main;
            }
         }
      }
   }
}
