@import 'global/index.scss';

// ##########################################################


.bg-banner {
   background-color: rgb(238, 242, 255);
}

.bannerSection {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding-top: 4rem;
   padding-bottom: 4rem;

   &-left {
      flex: 1;
      display: flex;
      justify-content: flex-start;

      img {
         width: 100%;
      }
   }
   &-center {
      flex: 1;
      text-align: center;

      &-head {
         font-size: 1.7rem;
         line-height: 2rem;
      }
      &-body {
         line-height: 1.75rem;
         padding-top: 1rem;
         padding-bottom: 1rem;
      }
      &-store {
         display: flex;
         margin-top: 1rem;
         flex-wrap: wrap;
         align-items: center;
         justify-content: center;

         div {
            padding-right: 1rem;
            height: 3rem;
            margin-top: 0.25rem;
            margin-bottom: 0.25rem;

            img {
               height: 100%;
            }
         }
      }
   }
   &-right {
      flex: 1;
      display: flex;
      justify-content: flex-end;

      img {
         width: 100%;
      }
   }
}
