@import 'global/index.scss';

.OrderDetail {
   margin-top: 2rem;
   margin-bottom: 2rem;
   padding: 2rem;
   text-transform: uppercase;
   background-color: $lighter;
   border-radius: $wonder-radius;

   &-intro {
      background-color: $orange-light;
      border-radius: $wonder-radius;
      padding: 0.75rem 1rem;
      font-size: small;
   }

   hr {
      border-radius: $wonder-radius;
      border-width: 0.1rem;
      border: none;
      outline: none;
      width: 100%;
      height: 0.25rem;
      background-color: $main-color;
   }

   &-head {
      display: flex;
      align-items: center;
      font-size: 1.25rem;
      margin-bottom: 2rem;
      
      &-title {
         flex: 2;
         font-size: 3rem;
         color: $main-color;
      }
      
      &-invoice{
         text-align: right;
         color: $main-color;

         &_no{
            margin-left: 1rem;
            color: black;
         }
         
         &_date{
            color: black;
            margin-left: 1rem;
         }
      }
   }

   &-body {
      margin-top: 2rem;

      &_address {
         margin-bottom: 2rem;
         &-name {
            color: $main-color;
         }
      }

      &_table {
         margin-bottom: 2rem;
         width: 100%;
         text-align: center;

         thead {
            tr {
               th {
                  color: $main-color;
               }
            }
         }
      }

      &_footer {
         &-body {
            margin-bottom: 2rem;
         }

         &-title {
            color: $main-color;
         }

         &-bottom {
            margin-top: 2rem;
         }
      }
   }
}
