@import 'global/index.scss';

.pointer {
   cursor: pointer;
}

.column-center {
   display: flex;
   flex-direction: column;
   align-content: space-between;
   justify-content: center;
}

.row-center {
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: center;
}

body::-webkit-scrollbar {
   // display: none;
}

.CartItem {
   padding: 1rem;
   display: flex;
   background-color: white;
   cursor: pointer;
   font-size: 0.9rem;
   transition: all 300ms ease-in-out;

   &:hover {
      background-color: $grey-bg-color;
   }

   &_left {
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;
      overflow: hidden;
      margin-right: 1rem;
   }

   &_right {
      flex: 2;

      &-name {
         line-height: 1.5rem;
         font-weight: 400;
         color: $text-color;
      }

      &-title {
         color: $head-body-color;
         font-size: 0.8rem;
         font-weight: 300;
         letter-spacing: 0.0625rem;
      }

      &-bottom {
         display: flex;
         margin-top: 0.5rem;
         justify-content: space-between;
         align-items: center;
         font-size: 1rem;

         &-price {
            line-height: 1.5rem;
            font-weight: bold;
         }

         &-inc-dec {
            background-color: $main-color;
            color: white;
            display: grid;
            grid-auto-flow: column;
            justify-items: center;
            align-items: center;
            column-gap: 0.4rem;
            padding: 0.3rem;
            box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;
            border-radius: 4px;
            font-weight: 600;

            .inc,
            .dec {
               cursor: pointer;
               font-weight: 700;
               padding-left: 0.35rem;
               padding-right: 0.35rem;
            }
         }

         &-del {
            color: rgb(248, 113, 113);
            font-size: 1.125rem;
         }
      }
   }
}

.discount {
   color: $text-color;
   font-size: 0.75rem;
   font-weight: 500;
   text-decoration: line-through;
}
