@import 'global/index.scss';
$orange-color: rgb(255, 237, 213/1);
// ##########################################################


.ad {
   background-color: $orange-color;
   border-radius: $border-radius;
   padding: 1.5rem 2rem;
   margin-top: 1.5rem;
   margin-bottom: 1.5rem;
   display: flex;
   align-items: center;
   justify-content: space-between;

   &-left {
      line-height: 1.75rem;

      &__title {
         text-transform: capitalize;
         font-size: 1.25rem;

         &-dark {
            font-weight: 700;
            color: $dark-main;
         }
      }

      &__body {
         color: lighten($color: $text-color, $amount: 20);

         .discount {
            text-transform: lowercase;
            color: $dark-main;
            text-decoration: none;
         }

         &-dark {
            font-weight: 600;
            color: $dark-main;
         }
      }
   }

   &-right {
      background-color: $main-color;
      border-radius: $border-radius * 5;
      // padding: 0.5rem 1rem;
      width: 7rem;
      height: 2.5rem;
      text-align: center;
      color: white;
      transition: 300ms;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 0.8rem;

      &:hover {
         color: white;
         box-shadow: 1px grey;
         background-color: $dark-main;
      }
   }
}
