@import 'global/index.scss';

.SideDashBoard {
   &-links {
      &-link {
         display: flex;
         color: black;
         align-items: center;
         padding: 0.75rem 1rem;
         margin: 0.25rem 0;
         font-size: 0.9rem;

         &-icon {
            margin-right: 0.5rem;
         }

         &:hover,
         &.active {
            background-color: $grey-bg-color;
            border-radius: $wonder-radius;
            color: $main-color;
         }
      }
   }
}
