@import 'global/index.scss';

$wonder-radius: 0.5rem;
// ##########################################################

.CategoryNav {
   padding-top: 1rem;
   padding-bottom: 1rem;
   color: black;
   text-transform: capitalize;
   display: flex;
   justify-content: space-between;
   align-items: center;
   font-size: 0.8rem;
   font-weight: 500;
   letter-spacing: 0.02rem;
   line-height: 1rem;
   background-color: white;

   &-left,
   &-right {
      display: grid;
      grid-auto-flow: column;
      column-gap: 2rem;
      justify-items: center;
      align-items: center;

      &-link {
         color: black;

         &.badge {
            color: red;
            padding: 0.35rem;
            background-color: lighten($color: pink, $amount: 5);
            border-radius: 0.1rem;
            position: relative;
         }
      }
   }
}
