@import 'global/index.scss';

$max-width: 420px;
// ##########################################################

.floatingCategory {
   position: fixed;
   z-index: 3;
   top: 0;
   max-width: $max-width;
   width: 100%;
   left: 0;
   bottom: 0;
   background-color: white;
   z-index: 21;
   box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 1000px 100px;
   text-transform: capitalize;
   color: white;
   font-family: 'Open sans', sans-serif;
   transition: all 300ms ease-in-out;
   animation-name: left_to_right;
   animation-duration: 309ms;
   animation-timing-function: linear;

   &-top {
      padding: 1rem 1.5rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 4rem;
      background-color: $main-color;

      &-name {
         font-weight: bold;
         font-size: 2rem;
         display: flex;

         i {
            padding-top: 0.25rem;
            padding-right: 0.25rem;
         }
      }

      &-close {
         color: $light-pink;
         width: 40px;
         height: 40px;
         border-radius: 50%;
         background-color: white;
         display: flex;
         align-items: center;
         justify-content: center;
         cursor: pointer;
         z-index: 2;
         transition: all ease-in-out 300ms;
         font-weight: 700;

         &:hover {
            background-color: $dark-pink;
            border: 0.1rem solid white;
         }
      }
   }

   &-bottom {
      top: 4rem;
      bottom: 0rem;
      position: fixed;
      max-width: $max-width;
      width: 100%;
      color: black;
      overflow-y: scroll;

      color: black;
      background-color: white;
      z-index: -1;

      &::-webkit-scrollbar {
         display: none;
      }

      &-top {
         padding: 1rem 1.5rem;
         font-weight: 600;
         font-size: 1rem;
      }

      &-footer {
         margin-top: 2rem;

         &-title {
            padding: 1rem 1.5rem;
            font-weight: 600;
         }

         &-links {
            padding: 2rem 1.5rem;
            padding-bottom: 1rem;
            font-weight: 600;
            padding-left: 0.75rem;

            &-link {
               padding: 0.125rem 1rem;
               padding-left: 0.5rem;
               display: block;
               margin-bottom: 1rem;
               font-size: medium;
               font-size: 1rem;
               border-radius: 5px;

               i {
                  margin-right: 0.5rem;
                  color: black;
               }

               span {
                  font-weight: lighter;
               }

               &:hover {
                  color: $main-color;
                  background-color: rgb(238, 242, 255);
               }
            }
         }
      }

      &-contet {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 1rem 1.5rem;
         background-color: $main-color;
         border-radius: 9px;
         height: 4rem;
         color: white;
         left: 0;
         bottom: 0;
         cursor: pointer;
         width: 100%;

         .amount {
            background-color: white;
            color: $dark-main;
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            font-weight: bolder;
         }
      }
   }

   &-center {
      position: fixed;
      max-width: $max-width;
      width: 100%;
      color: black;
      overflow-y: scroll;
      top: 4rem;
      bottom: 6rem;
      left: 0;

      &::-webkit-scrollbar {
         display: none;
      }

      &-content {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 1rem;
         background-color: $main-color;
         border-radius: 9px;
         height: 4rem;
         color: white;
         left: 0;
         bottom: 0;
         cursor: pointer;
         width: 100%;

         .amount {
            background-color: white;
            color: $dark-main;
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            font-weight: bolder;
         }
      }
   }
}

.close-shadow {
   @extend .floatingCategory;
   z-index: 20;
   max-width: 100vw;
   background-color: transparent;
   left: 0;
   right: 420px;
}
