@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap');
$main-d-color: #262521;
$main-l-color: #aaa180;

$lighter: #e2dcde;

$light: #f1e4e8;
$dark: #2d2d34;

$light-pink: #b97375;
$dark-pink: #ceb1be;

$red: rgb(220, 38, 38);
$red-light: lighten(
   $color: $red,
   $amount: 35
);

$orange: rgb(234, 88, 12);
$orange-light: lighten(
   $color: $orange,
   $amount: 35
);

$purple: rgb(79, 70, 229);
$purple-light: lighten(
   $color: $purple,
   $amount: 35
);

$green: rgb(5, 150, 105);
$green-light: rgb(209, 250, 229);

$main-color: $main-d-color;
$dark-main: darken($main-d-color, $amount: 20);

$text-color: rgb(55, 65, 81);
$head-body-color: rgb(75, 85, 99);

$card-shadow: 0 0 white, 0 0 white, 0 1px 2px 0 rgba(0, 0, 0, 0.05);
$mobile: 1020px;
$border-radius: 0.5rem;

.line-through {
   text-decoration: line-through;
}
$grey-color: rgb(75, 85, 99);

$light-orange-color: rgb(249, 115, 22);
$orange-color: orangered;

$dark-main: $orange-color;
$main-color: $light-orange-color;

$max-width: 420px;

$grey-bg-color: rgb(243, 244, 246/1);
$grey: rgb(156, 163, 175);
$wonder-radius: 0.5rem;

.b-grey {
   background-color: $grey-bg-color;
}

.danger {
   color: $orange-color;
   font-size: 0.9rem;
}

.orange-circle {
   border-radius: 50%;
   color: $orange;
   background-color: $orange-light;
}

.red-circle {
   border-radius: 50%;
   color: $red;
   background-color: $red-light;
}

.purple-circle {
   border-radius: 50%;
   color: $purple;
   background-color: $purple-light;
}

.border-bottom {
   border-bottom: 1px solid lighten($text-color, 60);
}

.border {
   border: 1px solid lighten($text-color, 60);
}

.green-circle {
   border-radius: 50%;
   color: $green;
   background-color: $green-light;
}

@keyframes left_to_right {
   from {
      left: -420px;
      top: 0px;
      opacity: 0;
   }
   to {
      left: 0px;
      top: 0px;
   }
}

@keyframes right_to_left {
   from {
      right: -420px;
      top: 0px;
      opacity: 0;
   }
   to {
      right: 0px;
      top: 0px;
   }
}
