@import 'global/index.scss';

$red: rgb(239, 68, 68);

$orange-color: rgb(255, 237, 213/1);
// ##########################################################

.main-bg {
   background-color: $main-color;
}

.line-through {
   text-decoration: line-through;
}

.bg-white {
   background-color: white;
}

.root {
   font-family: 'Open sans', sans-serif;
   position: relative;
   scroll-behavior: smooth;
}

.rounded-edges {
   border-radius: 0.35rem;
}

.font-600 {
   font-weight: 600;
}

.font-700 {
   font-weight: 700;
}

.max-width {
   max-width: 1536px;
   margin: auto;
   overflow-x: hidden;
}
.text-main{
   
}
.relative {
   position: relative;
}

.hover-green {
   text-decoration: none;
   text-transform: capitalize;
   transition: 200ms;
   cursor: pointer;
}

.hover-green:hover,
.hover-green:focus {
   color: $main-color;
}

.green-badge {
   background-color: $green-light;
   color: $green;
   border-radius: 1000px;
   padding: 0.25rem 0.75rem;
   cursor: pointer;
   transition: all 400ms ease-in-out;

   &:hover {
      background-color: $green;
      color: white;
   }
}

.sticky {
   position: sticky;
   top: 0;
   z-index: 20;
}

.d-flex {
   display: flex;
}

.justify-between {
   justify-content: space-between;
}

.flex-wrap {
   flex-wrap: wrap;
}

.text-main {
   color: $main-color;
}

.mobile {
   display: none;
}

.d-none {
   display: none;
}

.m-x {
   margin-left: 2.5rem;
   margin-right: 2.5rem;
}

.flex-center {
   display: flex;
   justify-content: center;
   align-items: center;
}

.align-center {
   display: flex;
   align-items: center;
}

a,
i {
   position: relative;

   .float-top-right {
      width: 0;
      height: 0;
      position: absolute;
      top: -0.25rem;
      right: -0.5rem;
      font-size: 0.75rem;
      background-color: orangered;
      font-weight: bold;
      border-radius: 50%;
      text-align: center;
      padding: 0.6rem;
      display: flex;
      align-items: center;
      justify-content: center;
   }
}

.ml-0 {
   margin-left: 0 !important;
}

.w-100 {
   width: 100%;
}

@media (max-width: $mobile) {
   .p-x {
      padding-left: 1.5rem;
      padding-right: 1.5rem;
   }
   .mob-py-07 {
      padding-top: 0.7rem;
      padding-bottom: 0.7rem;
   }
   .mob-mx-0 {
      margin-left: 0;
      margin-right: 0;
      margin-inline: 0;
   }
   .desktop {
      display: none;
   }
   .mobile {
      display: block;
   }
   .root {
      margin-bottom: 4rem;
   }
   .mp-2 {
      padding: 2rem;
   }
}
