@import 'global/index.scss';

// ##########################################################


.floatingCategory-bottom-content-item {
   padding: 0.25rem 1rem;

   &-name {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      font-size: 1rem;
      padding: 0.25rem 0.5rem;
      border-radius: 5px;

      img{
         width: 1rem;
         margin-right: .5rem;
      }

      &:hover {
         color: $main-color;
         background-color: rgb(238, 242, 255);
      }
   }

   &-sub {
      padding-left: 1rem;
      padding-top: 0.5rem;
      font-size: 0.9rem;

      a {
         display: flex;
         align-items: center;
         padding: .125rem 0;

         span {
            padding-left: 0.5rem;
            display: block;
            // color: $text-color;
         }
      }
   }
}
