@import 'global/index.scss';
.Orders {
      text-transform: capitalize;

      &-title {
         margin-bottom: 1.5rem;
      }

      .rounded-edges {
         @extend .border;
         overflow: hidden;
      }

      &-table {
         width: 100%;
         border-collapse: collapse;
         font-size: 0.8rem;
         overflow-x: scroll;

         &-thead {
            text-transform: uppercase;
            font-size: .9rem;
            color: $text-color;
            background-color: $grey-bg-color;

            &-tr {
               &-td {
                  letter-spacing: 0.05em;
                  padding-top: 0.5rem;
                  padding-bottom: 0.5rem;
               }
            }
         }

         tbody {
            text-align: center;
            padding: 1rem;
            font-weight: 400;

            tr {
               @extend .border-bottom;

               td {
                  padding-top: 0.75rem;
                  padding-bottom: 0.75rem;

                  &.pending {
                     color: $light-orange-color;
                  }
                  &.cancel {
                     color: $red;
                  }
                  &.delivered {
                     color: $green;
                  }
                  &.processing {
                     color: $purple;
                  }
               }

               &:last-child {
                  border-bottom: none;
               }
            }
         }
      }
   }