@import 'global/index.scss';

.pointer {
   cursor: pointer;
}

.column-center {
   display: flex;
   flex-direction: column;
   align-content: space-between;
   justify-content: center;
}

.row-center {
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: center;
}

.product {
   width: 100%;
   padding: 1rem 1rem;
   background-color: white;
   box-shadow: $card-shadow;
   @extend .column-center;
   min-height: 8rem;
   font-size: 0.9rem;
   transition: all 300ms ease-in-out;
   position: relative;

   &_top {
      width: 100%;
      overflow: hidden;
      @extend .pointer;

      img {
         width: 100%;
         transition: all 300ms ease-in-out;

         &:hover {
            transform: scale(1.2);
         }
      }
   }

   &_bottom {
      @extend .column-center;
      flex: 1;
      line-height: 1.125rem;
      color: black;

      &-qty {
         color: $head-body-color;
         font-size: 0.75rem;
         font-weight: 300;
      }

      &-name {
         line-height: 1.5rem;
         font-weight: 400;
         color: $text-color;
      }

      &-price {
         line-height: 1.5rem;
         @extend .row-center;
         font-size: 1rem;

         &-amount {
            font-weight: bold;
         }

         .inc-dec {
            background-color: $main-color;
            color: white;
            display: grid;
            grid-auto-flow: column;
            justify-items: center;
            align-items: center;
            column-gap: 0.4rem;
            padding: 0.3rem;
            box-shadow: $card-shadow;
            border-radius: 4px;
            font-weight: 600;

            .inc,
            .dec {
               cursor: pointer;
               font-weight: 700;
               padding-left: 0.35rem;
               padding-right: 0.35rem;
            }
         }

         &-cart {
            padding-bottom: 0;
            margin-bottom: 0;
            width: 2rem;
            height: 2rem;
            @extend .row-center;
            @extend .pointer;
            justify-content: center;
            font-size: 1.2rem;
            transition: all 300ms ease-in-out;
            box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;
            color: $main-color;

            &:hover {
               background-color: $main-color;
               color: white;
               border: none;
            }
         }
      }

      &_right {
         display: flex;
         flex-direction: column;
         flex: 1;
         margin-left: 1rem;

         span {
            &:hover {
               color: $main-color;
            }
         }
      }
   }

   &_stock,
   &_discount {
      position: absolute;
      top: 1rem;
      padding: 0.25rem 0.5rem;
      border-radius: 9999px;
      color: rgb(220, 38, 38);
      background-color: rgb(254, 226, 226);
      z-index: 10;
      font-weight: 600;
      line-height: 1rem;
      font-size: 0.75rem;
   }

   &_discount {
      right: 1rem;
      background-color: $orange-color;
      font-weight: 500;
      color: white;
      border-radius: 0.25rem;
   }
}

.discount {
   color: $text-color;
   font-size: 0.75rem;
   font-weight: 500;
}
