@import 'global/index.scss';

.ProfileView {
   &-title {
      margin-bottom: 1rem;
      text-transform: capitalize;
   }

   @media (max-width: $mobile) {
      width: 100%;
   }

   &-form {
      width: 100%;
      color: $text-color;
      font-size: small;

      .grid-50 {
         display: grid;
         column-gap: 1rem;
         grid-template-columns: repeat(2, minmax(0, 1fr));

         @media (max-width: $mobile) {
            grid-template-columns: repeat(1, minmax(0, 1fr));
         }
      }

      &-container {
         &-img {
            border-radius: 0.325rem;
            margin-top: 1rem;
            max-width: 100px;
            width: 100%;
            padding: 0.2rem;
            border: 0.1rem solid $grey;
         }

         &_visible {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            border-radius: 0.325rem;
            padding: 2rem;

            border: 0.13rem dashed $grey;
            cursor: pointer;
            box-shadow: none;
            line-height: 1.5rem;

            &-icon {
               font-size: 2rem;
               color: $main-color;
            }

            &-name {
               color: black;
               font-size: small;
            }

            &-note {
               color: $grey;
               font-size: smaller;
            }
         }

         &-label {
            line-height: 2rem;
            text-transform: capitalize;
            color: $grey-color;
         }

         #photo {
            height: 7rem;
            border: 2px dashed $grey-color;
            cursor: pointer;
            box-shadow: none;
         }

         &-input {
            width: 100%;
            border: 0.0000000000625px solid $text-color;
            border-radius: $border-radius * 0.5;
            padding: 0.9rem;
            border: none;
            outline: none;
            flex: 1;
            background-color: transparent;
            transition: border 400ms ease;
            position: relative;
            box-shadow: 0px 0 2px 0.000125px #777;
            z-index: 1;
            margin-bottom: 1rem;

            &::placeholder {
               color: grey;
               text-transform: capitalize;
               font-size: small;
            }

            &:focus {
               border: 0.000625px solid $main-color;
               box-shadow: none;
            }

            &.error {
               border: 0.000625px solid $red;
               box-shadow: none;
            }
         }
      }

      &-submit {
         background-color: $main-color;
         border-radius: $border-radius * 0.5;
         border: none;
         text-align: center;
         padding: 1rem 0.9rem;
         flex: 1;
         float: right;
         margin-top: 1rem;
         text-transform: capitalize;
         font-size: small;
         color: white;
         transition: all 400ms ease;
         cursor: pointer;

         &:hover {
            background-color: $orange;
         }
      }
   }
}
