@import 'global/index.scss';

$head-body-color: rgb(75, 85, 99);
// ##########################################################

.bg-grey {
   background-color: rgb(243, 244, 246);
}

.featuredCategories {
   color: $text-color;
   padding-top: 3rem;
   padding-bottom: 3rem;

   &-head {
      color: black;
      line-height: 2.5rem;
      padding-bottom: 2rem;
      width: 40%;
      margin: auto;
      @media (max-width: 1000px) {
         width: 100%;
      }

      &__title {
         text-transform: capitalize;
         font-weight: 600;
         letter-spacing: 0.01rem;
         display: block;
         font-size: 1.5rem;
         text-align: center;
      }

      &__body {
         line-height: 1.5rem;
         text-align: center;
         color: $head-body-color;
      }
   }

   &-list {
      width: 100%;
      display: grid;
      gap: 0.4rem 0.3rem;
      justify-items: center;
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
      text-transform: capitalize;
      @media (max-width: 1100px) {
         grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
      }
      @media (max-width: 600px) {
         grid-template-columns: repeat(2, minmax(0, 1fr));
      }

      &-item {
         width: 100%;
         padding: 1.5rem 1rem;
         background-color: white;
         box-shadow: $card-shadow;
         display: flex;
         flex-direction: column;
         align-content: space-between;
         min-height: 8rem;
         font-size: 0.9rem;
         cursor: pointer;
         transition: all 300ms ease-in-out;

         &_top {
            text-align: center;
            padding-bottom: 1rem;
            overflow-x: hidden;

            &:hover {
               color: $main-color;
            }
         }
         &_bottom {
            font-size: 0.75rem;
            flex: 1;
            line-height: 1.125rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: lighten($color: $text-color, $amount: 30);

            &_right {
               display: flex;
               flex-direction: column;
               flex: 1;
               margin-left: 1rem;

               span {
                  &:hover {
                     color: $main-color;
                  }
               }
            }
         }
         img {
            width: 30%;
         }
      }
   }
}
