@import 'global/index.scss';

// ##########################################################

.topNav {
   display: flex;
   width: 100%;
   font-size: 1rem;
   color: $text-color;
   line-height: 1rem;
   font-size: medium;
   font-weight: 400;
   letter-spacing: 0.0125rem;
   font-size: 0.7em;
   padding: 0.5rem 1rem;
   justify-content: space-between;
   align-items: center;

   &_left {
      display: grid;
      grid-auto-flow: column;
      column-gap: 0.5rem;
      justify-items: center;

      a {
         color: $main-color;
         font-weight: bold;
      }
   }

   &_right {
      display: grid;
      grid-auto-flow: column;
      column-gap: 0.5rem;
      justify-items: center;
      align-items: center;

      .vertical-line {
         width: 1.25px;
         height: 70%;
         content: '';
         background-color: $text-color;
      }

      a {
         display: grid;
         grid-auto-flow: column;
         column-gap: 0.125rem;
         justify-items: center;

         i {
            font-size: 0.9rem;
         }
      }
   }
}

a {
   text-decoration: none;
   text-transform: capitalize;
   color: $text-color;
   transition: 300ms;
}

a:hover,
a:focus {
   color: $main-color;
}
.p-x {
   padding-left: 2.5rem;
   padding-right: 2.5rem;
}

@media (max-width: 1020px) {
   .p-x {
      padding-left: 0.75rem;
      padding-right: 0.75rem;
   }
}
