@import 'global';

.Dashboard {
   display: grid;
   column-gap: 3rem;
   grid-auto-flow: column;
   justify-content: left;
   background-color: $grey-bg-color;
   grid-template-columns: 1fr 3fr;
   padding-top: 3.5rem;

   @media (max-width: $mobile) {
      display: flex;
      flex-direction: column;
   }

   & > div {
      border-radius: $wonder-radius * 0.5;
      background-color: white;
      padding: 2rem;

      @media (max-width: $mobile) {
         // display: flex;
         flex: 1;
         margin-top: 2rem;
      }
   }
}
