@import 'global/index.scss';

// ##########################################################


@media (max-width: $mobile) {
   .floatingSection {
      width: 100%;
      position: fixed;
      z-index: 20;
      bottom: 0;
      padding-top: 1rem;
      padding-bottom: 1rem;
      padding-left: 2.5rem;
      padding-right: 2.5rem;
      font-size: 1.5rem;
      color: white;
      display: flex !important;
      justify-content: space-between;
      align-items: center;

      &-icon {
         color: white;
         cursor: pointer;

         a {
            color: white;
         }
      }
   }
}
