@import 'global/index.scss';

// ##########################################################

.floatingShopCart {
   position: fixed;
   z-index: 3;
   top: 0;
   max-width: $max-width;
   width: 100%;
   right: 0;
   bottom: 0;
   background-color: white;
   z-index: 21;
   box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 1000px 100px;
   text-transform: capitalize;
   transition: all 300ms ease-in-out;
   animation-name: right_to_left;
   animation-duration: 309ms;
   animation-timing-function: linear;

   &-top {
      padding: 1rem;
      display: flex;
      justify-content: space-between;
      height: 4rem;

      &-name {
         font-weight: bold;
         font-size: 1.3rem;
         display: flex;
         align-items: center;

         i {
            padding-top: 0.25rem;
            padding-right: 0.25rem;
         }
      }
      &-close {
         color: $grey-color;
         font-size: 0.8rem;
         display: flex;
         align-items: center;
         cursor: pointer;

         i {
            padding-top: 0.25rem;
         }

         &:nth-child() {
            font-size: inherit;
         }
      }
   }

   &-bottom {
      position: fixed;
      max-width: 420px;
      width: 100%;
      bottom: 0;
      color: black;
      padding: 1rem 1rem;
      background-color: white;

      &-content {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 1rem;
         background-color: $main-color;
         border-radius: 9px;
         height: 4rem;
         color: white;
         right: 0;
         bottom: 0;
         cursor: pointer;
         width: 100%;

         .amount {
            background-color: white;
            color: $dark-main;
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            font-weight: bolder;
         }
      }
   }

   &-center {
      position: fixed;
      max-width: $max-width;
      width: 100%;
      color: black;
      overflow-y: scroll;
      top: 4rem;
      bottom: 6rem;
      right: 0;

      &::-webkit-scrollbar {
         display: none;
      }

      &-content {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 1rem;
         background-color: $main-color;
         border-radius: 9px;
         height: 4rem;
         color: white;
         right: 0;
         bottom: 0;
         cursor: pointer;
         width: 100%;

         .amount {
            background-color: white;
            color: $dark-main;
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            font-weight: bolder;
         }
      }
   }
}

.close-shadow {
   @extend .floatingShopCart;
   z-index: 20;
   max-width: 100vw;
   background-color: transparent;
   left: 0;
   right: 420px;
}
