@import 'global/index.scss';

// ##########################################################

.top-footer {
   display: grid;
   grid-auto-flow: column;
   column-gap: 1rem;
   padding-top: 1rem;
   padding-bottom: 1rem;

   &-child {
      display: flex;
      align-items: center;
      text-transform: capitalize;
      font-size: 0.8rem;
      justify-content: flex-start;

      i {
         color: $main-color;
         font-size: 1.25rem;
         margin-right: 1rem;
      }
   }
   .vertical-line {
      width: 1px;
      height: 100%;
      content: '';
      background-color: lighten($grey-color, 50);
      box-shadow: $card-shadow;
   }
}
.footer {
   display: grid;
   grid-auto-flow: dense;
   gap: 2rem;
   display: grid;
   justify-items: flex-start;
   text-transform: capitalize;
   grid-template-columns: repeat(4, minmax(0, 1fr));
   padding-top: 3rem;
   padding-bottom: 3rem;

   @media (max-width: $mobile) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
   }
   @media (max-width: 600px) {
      // padding-left: 0.75rem !important;
      // padding-right: 0.75rem !important;
   }
   &-child {
      display: flex;
      flex-direction: column;

      &-head {
         font-size: 1.2rem;
      }

      &-list {
         margin-top: 1rem;
         display: flex;
         flex-direction: column;
         font-size: 0.875rem;
         line-height: 1.875rem;

         span {
            font-size: 0.875rem;
            line-height: 1.5rem;
            color: $grey-color;
         }
      }
   }
}
.text-lower {
   text-transform: lowercase;
}
.bottom-footer {
   color: $grey-color;
   display: block;
   text-align: center;
   padding-top: 1rem;
   padding-bottom: 1rem;
   font-size: 0.8rem;

   a {
      color: $main-color;
   }
}
