@import 'global/index.scss';

.ChangePasswordView {
   &-title {
      margin-bottom: 1rem;
   }

   @media (max-width: $mobile) {
      width: 100%;
   }

   &-form {
      color: $text-color;

      &-label {
         line-height: 2rem;
         font-size: 0.9rem;
         text-transform: capitalize;
         color: $grey-color;
      }

      &-input {
         width: 100%;
         border: 0.0000000000625px solid $text-color;
         border-radius: $border-radius * 0.5;
         padding: 0.9rem;
         border: none;
         outline: none;
         flex: 1;
         background-color: transparent;
         transition: border 400ms ease;
         position: relative;
         box-shadow: 0px 0 2px 0.000125px #777;
         z-index: 1;
         margin-bottom: 1rem;
         font-size: 0.9rem;

         &::placeholder {
            color: grey;
            text-transform: capitalize;
            font-size: 0.85rem;
         }

         &:focus {
            border: 0.000625px solid $main-color;
            box-shadow: none;
         }

         &.error {
            border: 0.000625px solid $red;
            box-shadow: none;
         }
      }

      &-input {
         &-container {
            display: flex;
            font-size: 1.5rem;
            padding: 0 0.25rem;
            margin-bottom: 1rem;
            position: relative;
            z-index: 1;

            &-icon {
               font-size: 1.25rem;
               color: gray;
               position: absolute;
               vertical-align: middle;
               margin-left: 0.5rem;
               margin-top: 0.5rem;
               z-index: 2;
               display: flex;
               align-items: center;
               top: 12.5%;
            }
         }
      }

      &-submit {
         background-color: $main-color;
         border-radius: $border-radius * 0.5;
         border: none;
         text-align: center;
         padding: 1rem 0.9rem;
         flex: 1;
         float: right;
         margin-top: 1rem;
         text-transform: capitalize;
         font-size: 1.125rem;
         color: white;
         transition: all 400ms ease;
         cursor: pointer;

         &:hover {
            background-color: $orange;
         }
      }
   }
}

.custom-link {
   color: $text-color;
   cursor: pointer;
   padding-left: 0.25rem;
   font-weight: 700;
   text-decoration: underline;

   &:hover {
      text-decoration: none;
      color: $main-color;
   }
}
