@import 'global/index.scss';

.MainDashBoard {
   &-title {
      font-size: 1.25rem;
   }

   &-cards {
      display: grid;
      grid-auto-flow: column;
      column-gap: 1rem;
      margin-top: 1.5rem;

      @media (max-width: $mobile) {
         display: flex;
         flex-wrap: wrap;
         justify-content: space-between;
         flex: 50%;
      }

      @media (max-width: $mobile) {
      }

      &-card {
         margin-bottom: 2rem;
         display: flex;
         column-gap: 1rem;
         border: 1px solid $grey-bg-color;
         padding: 1.5rem 1rem;
         align-items: center;
         text-transform: capitalize;
         border-radius: $wonder-radius * 0.5;

         @media (max-width: $mobile) {
            width: 48%;
         }

         @media (max-width: 527px) {
            width: 100%;
         }

         &-icon {
            width: 3rem;
            height: 3rem;
            font-size: 1.25rem;
            line-height: 1.75rem;
            display: flex;
            justify-content: center;
            align-items: center;
         }

         &-main {
            &-name {
               display: flex;
               font-size: 0.9rem;
            }

            &-number {
               font-weight: 700;
            }
         }
      }
   }
}
