@import 'global/index.scss';

$max-width: 600px;
// ##########################################################

.fixed-body {
   position: fixed;
   z-index: 3;
   top: 0;
   display: flex;
   align-items: center;
   width: 100%;
   right: 0;
   bottom: 0;
   z-index: 21;
   background-color: transparent;
   text-transform: capitalize;
   padding-left: 0.75rem;
   padding-right: 0.75rem;
   overflow-y: auto;
   font-size: 80%;

   &::-webkit-scrollbar {
      display: none;
   }

   .auth {
      position: relative;
      z-index: 1;
      max-width: 32rem;
      width: 100%;
      background-color: #fefefe;
      margin: auto;
      background-color: white;
      padding: 1.75rem;
      border-radius: 1rem;
      overflow-y: auto;

      @media (max-width: $max-width) {
         line-height: 3rem;
         padding: 3rem;
      }

      &::-webkit-scrollbar {
         display: none;
      }

      @media (max-width: $max-width) {
         display: block;
      }

      &-top {
         line-height: 2.5rem;
         text-align: center;
         margin-bottom: 1rem;

         &-title {
            text-transform: capitalize;
            font-weight: 700;
            font-size: 2rem;
         }

         &-sub-title {
            font-size: 1rem;
            color: grey;
            text-transform: none;
         }
         &-error {
            display: none;
            &.danger {
               display: inline-block;
            }
         }
      }

      &-form {
         color: $text-color;

         &-input {
            &-label {
               line-height: 2rem;
               font-size: 1rem;
               text-transform: capitalize;
               padding-left: 0.25rem;
            }

            &-container {
               display: flex;
               font-size: 1.5rem;
               padding: 0 0.25rem;
               margin-bottom: 1rem;
               position: relative;
               z-index: 1;

               &-icon {
                  font-size: 1.25rem;
                  color: gray;
                  position: absolute;
                  vertical-align: middle;
                  margin-left: 0.5rem;
                  margin-top: 0.5rem;
                  z-index: 2;
                  display: flex;
                  align-items: center;
                  top: 12.5%;
               }

               &-input {
                  border: 0.000625px solid $text-color;
                  border-radius: $border-radius * 0.5;
                  padding: 1rem;
                  padding-left: 2.5rem;
                  border: none;
                  outline: none;
                  flex: 1;
                  background-color: transparent;
                  transition: border 400ms ease;
                  position: relative;
                  box-shadow: 0px 0 2.5px 0.00125px #777;
                  z-index: 1;

                  &::placeholder {
                     color: grey;
                     text-transform: capitalize;
                  }

                  &:focus {
                     border: 0.000625px solid $main-color;
                     box-shadow: none;
                  }
               }
            }
         }

         &-forgot_password {
            @extend .custom-link;
         }

         &-button {
            display: block;
            width: 100%;
            background-color: $main-color;
            border-radius: $border-radius * 0.5;
            border: none;
            text-align: center;
            padding: 1rem 0.9rem;
            flex: 1;
            margin-top: 1rem;
            text-transform: capitalize;
            font-size: 1.125rem;
            color: white;
            transition: all 400ms ease;
            cursor: pointer;

            &:hover {
               background-color: $dark-main;
            }
         }
      }

      &-center {
         margin: 2rem 0;
         font-size: 1.125rem;
         text-align: center;
         text-transform: uppercase;
      }

      &-bottom {
         transition: all 400ms ease;
         display: block;

         @media (min-width: $mobile) {
            display: flex;
         }

         &-google,
         &-facebook {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: $border-radius * 0.5;
            border: none;
            text-align: center;
            padding: 0.75rem 0.9rem;
            flex: 1;
            margin-top: 1rem;
            text-transform: capitalize;
            font-size: 1.125rem;
            background-color: lighten($color: #eee, $amount: 0);
            font-weight: 600;
            color: $text-color;
            transition: all 400ms ease;
            cursor: pointer;

            @media (min-width: $mobile) {
               padding: 1rem 0;
               display: block;
            }

            &-icon {
               margin-right: 0.5rem;
            }
            &:hover {
               color: white;
            }
         }

         &-facebook {
            margin-right: 0.5rem;

            &:hover {
               background-color: lighten($color: #0000ff, $amount: 10);
            }
         }

         &-google {
            &:hover {
               background-color: crimson;
            }
         }
      }

      &-register {
         padding-top: 1rem;
         margin: 0;
         text-transform: none;

         @media (min-width: $mobile) {
            color: grey;
         }

         &-link {
            @extend .custom-link;
         }
      }

      &-left {
         flex: 1;
         display: flex;
         align-items: center;

         @media (max-width: $max-width) {
            flex: 2;
         }

         img {
            width: 100%;
         }
      }

      &-right {
         flex: 1;
         color: black;

         &-name {
            font-size: 1.5rem;
            font-weight: bold;
            text-transform: capitalize;
            display: block;
            margin-bottom: 0.75rem;
         }

         &-title {
            color: rgb(220, 38, 38);
            background-color: rgb(254, 226, 226);
            text-align: center;
            font-family: Inter, sans-serif;
            text-transform: capitalize;
            width: fit-content;

            font-size: 0.75rem;
            line-height: 1rem;
            font-weight: 600;

            padding: 0.25rem 0.5rem;
            border-radius: 9999px;
            display: inline-block;
            margin-bottom: 0.75rem;

            &.available {
               color: rgb(5, 150, 105);
               background-color: rgb(209, 250, 229);
            }
         }

         &-body {
            font-size: 0.75rem;
            line-height: 1.3rem;
            color: rgb(55, 65, 81);
            margin-bottom: 0.75rem;
         }

         &-price {
            font-weight: 700;
         }

         &-cart {
            display: inline-flex;
            margin-top: 1rem;
            font-weight: 700;
            box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;

            div {
               padding: 0.5rem 2rem;
               cursor: pointer;
               font-size: 1.2rem;
               box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;
            }

            input {
               border: none;
            }

            &-add {
               padding-bottom: 0;
               margin-bottom: 0;
               width: 2rem;
               height: 2rem;
               display: flex;
               justify-content: center;
               align-items: center;
               cursor: pointer;
               justify-content: center;
               font-size: 1.2rem;
               transition: all 300ms ease-in-out;
               box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;
               color: $main-color;

               &:hover {
                  background-color: $main-color;
                  color: white;
                  border: none;
               }
            }

            &-left,
            &-right {
               box-shadow: $card-shadow, $card-shadow, $card-shadow, $card-shadow, $card-shadow;
               border: none;
               transition: all 400ms ease;
               font-weight: 700;
            }

            &-left:hover,
            &-right:hover {
               color: $text-color;
            }

            &-quantity {
               border-left: none;
               border-right: none;
               outline: none;
               width: 100px;
               text-align: center;
               font-size: 1.2rem;
               border: 1px solid black;
            }

            &-right {
            }
         }

         &-bottom {
            margin: 1rem 0;
            color: rgb(55, 65, 81);
            font-weight: 600;

            &-head {
               color: $text-color;
               font-weight: 700;
            }

            &-categories {
               display: inline-flex;
               letter-spacing: 0.0618rem;

               &-category {
                  font-size: 0.9rem;
                  background-color: lighten($color: rgb(55, 65, 81), $amount: 70);
                  padding: 0.1rem 0.5rem;
                  border-radius: 1000px;
               }
            }

            &-tags {
               &-tag {
                  font-size: 0.75rem;
                  background-color: lighten($color: rgb(55, 65, 81), $amount: 70);
                  padding: 0.1rem 0.5rem;
                  border-radius: 1000px;
                  display: inline-block;
                  margin-top: 1rem;
                  margin-left: 1rem;
                  letter-spacing: 0.01rem;
               }
            }
         }
      }
   }

   .close {
      position: absolute;
      color: $light-pink;
      top: 1rem;
      right: 1rem;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      z-index: 2;
      transition: all ease-in-out 300ms;
      font-weight: 700;

      &:hover {
         background-color: $dark-pink;
         border: 0.1rem solid white;
      }
   }
}

.close-shadow {
   @extend .auth;
   z-index: 20;
   opacity: 0.6;
   max-width: 100vw;
   background-color: black;
   top: 0;
   left: 0;
   right: 0;
}

.custom-link {
   color: $text-color;
   cursor: pointer;
   padding-left: 0.25rem;
   font-weight: 700;
   text-decoration: underline;

   &:hover {
      text-decoration: none;
      color: $main-color;
   }
}
