@import 'global/index.scss';

// ##########################################################

.bg-main {
   background-color: $main-color;
   border-radius: $wonder-radius;
   padding: 4rem;
   margin-top: 1rem;
   margin-bottom: 1rem;
}

img {
   width: 100%;
}
.bg {
   background-color: rgb(249, 250, 251);
}
.quickDelivery {
   border-radius: $wonder-radius;
   padding: 1.5rem 2.5rem;
   background-color: white;
   display: flex;
   align-items: center;
   justify-content: space-between;
   flex: 1;

   &-left {
      flex: 3;

      @media (max-width: 1100px) {
         padding-right: 0rem;
      }

      &__title {
         line-height: 1.75rem;
         text-transform: capitalize;
         font-size: 1rem;
         letter-spacing: 0.00001rem;

         &-dark {
            font-weight: 700;
            color: $dark-main;
         }
      }

      &__top {
         font-weight: bold;
         font-size: 1.125rem;
         text-transform: capitalize;
      }

      &__body {
         margin-top: 0.5rem;
         line-height: 1.5rem;
         font-size: 0.875rem;

         @media (max-width: $mobile) {
            line-height: 1.5rem;
         }

         a {
            display: inline-block;
            background-color: $main-color;
            border-radius: $wonder-radius * 5;
            margin-top: 2rem;
            padding: 0.8rem 1rem;
            color: white;
            font-size: 1rem;
            word-wrap: break-word;
            @media (max-width: $mobile) {
               margin-top: 1rem;
            }
         }

         .discount {
            text-transform: lowercase;
            color: $dark-main;
            text-decoration: none;
         }

         &-dark {
            font-weight: 600;
            color: $dark-main;
         }
      }
   }

   &-right {
      display: flex;
      transition: 300ms;
      flex: 2;
      justify-content: flex-end;

      img {
         width: 80%;
         justify-self: flex-end;
      }
   }
}
