@import 'global/index.scss';

.nav {
   padding-top: 1rem;
   padding-bottom: 1rem;
   color: white;
   font-weight: 700;
   display: flex;
   justify-content: space-between;
   align-items: center;
   flex: 1;

   &-logo {
      img {
         min-height: 100%;
         height: 2rem;
      }
   }

   &-search {
      flex: 1;
      background-color: white;
      margin-inline: 4rem;
      margin-left: 4rem;
      margin-right: 4rem;
      border-radius: $border-radius;
      display: flex;
      align-items: center;

      input {
         flex: 1;
         border-radius: $border-radius;
         border: none;
         outline: none;
         padding: 1rem;

         &::placeholder {
            color: lighten($color: $text-color, $amount: 40);
         }
      }

      &__icon {
         color: lighten($color: $text-color, $amount: 40);
         font-size: 1.5rem;
         line-height: 2rem;
         margin-right: 1rem;
      }
   }

   &-right {
      display: grid;
      grid-auto-flow: column;
      column-gap: 2rem;

      i,
      a {
         font-size: 1.5rem;
         line-height: 2rem;
         position: relative;
         z-index: 1;
         color: white;
      }
   }
}
